import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { BaseResponse } from 'src/app/Model/base-response';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BranchDTO, BranchWithDetail } from 'src/app/pages/branch/view-branch/view-branch.component';
import { BaseApiHandlerService } from '../base-api-handler.service';
export class BranchDetails{
  branchManager:string;
  totalExpense:number;
  netSales:number;
  grossProfit:number;
  email:string;
  phoneNumber:string;
  Receviable:number;
}
export  class SalesAgentReport {
  name: string;
  amount: number;
  percentage: number;
}

export  class SalesReport {
  branchName: string;
  totalSales: number;
  salesAgents: SalesAgentReport[];
  details: SalesAgentReport[];
}


@Injectable({
  providedIn: 'root'
})
export class BranchService extends BaseApiHandlerService {


  branchObserver$: Observable<BranchWithDetail>;
  branchDetailsObserver$:Observable<BranchDetails>;
  private branchSubject$: BehaviorSubject<BranchWithDetail> = new BehaviorSubject<BranchWithDetail>(undefined);
  private branchDetailSubject$: BehaviorSubject<BranchDetails> = new BehaviorSubject<BranchDetails>(undefined);

  constructor(private http:HttpClient) {

    super(http);
    this.branchObserver$= this.branchSubject$.asObservable();
    this.branchDetailsObserver$=this.branchDetailSubject$.asObservable();
   }

   public get branchesWithDetail() { return this.branchSubject$.value; }

   SaveBranch(data:BranchDTO) {
    return this.Post(data, API_URL + API_ENDPOINTS.Branch );
  }
  UpdateBranch(data: BranchDTO) {
    return this.Update(data.id, data, API_URL + API_ENDPOINTS.Branch);
  }
  GetBranches() {
    this.GetAll(API_URL + API_ENDPOINTS.Branch).subscribe(res => {
      this.branchSubject$.next(res.dynamicResult);
    });
  }
  GetBranchAsync(){
    return this.GetAll(API_URL+API_ENDPOINTS.Branch+"/GetBranchAsync");
  }
  GetBranchDetailsAsync(month:number, branchId:string)
  {
    this.GetAll(API_URL + API_ENDPOINTS.Branch+"/GetBranchDetailsAsync?month="+month+"&branchId="+branchId
    ).subscribe(res => {
      this.branchDetailSubject$.next(res.dynamicResult);
    });
  }
  GetAgentBranchSales(month:number,branchId:string){
    return this.GetAll(API_URL+API_ENDPOINTS.Branch+`/GetAgentsSalesForBranchPerMonth?month=${month}&branchId=${branchId}`)
    ;
  }
  GetAgentARForBranchPerMonth(month:number,branchId:string){
    return this.GetAll(API_URL+API_ENDPOINTS.Branch+`/GetAgentARForBranchPerMonth?month=${month}&branchId=${branchId}`);
  }
  GetBrokersAPForBranchPerMonth(month:number,branchId:string){
    return this.GetAll(API_URL+API_ENDPOINTS.Branch+`/GetBrokersAPForBranchPerMonth?month=${month}&branchId=${branchId}`);
  }
  GetAgentRelatedBranchWithOpenBalanceByMonthOrDate(branchId: string, pageRowCount?: number, page?: number,
    month?: number, from?: Date, to?: Date, nameQuery?: string, isExcel?: boolean, isPdf?: boolean, variable?: string) {
    let queryParams = [];

    if (month) {
      queryParams.push(`month=${encodeURIComponent(month)}`);
    }
    if (from) {
      queryParams.push(`from=${encodeURIComponent(from.toISOString())}`);
    }
    if (to) {
      queryParams.push(`to=${encodeURIComponent(to.toISOString())}`);
    }
    if (nameQuery) {
      queryParams.push(`nameQuery=${encodeURIComponent(nameQuery)}`);
    }
    if (isExcel) {
      queryParams.push(`isExcel=${encodeURIComponent(isExcel)}`);
    }
    if (isPdf) {
      queryParams.push(`isPDF=${encodeURIComponent(isPdf)}`);
    }
    if (pageRowCount) {
      queryParams.push(`pageRowCount=${encodeURIComponent(pageRowCount)}`);
    }
    if (page) {
      queryParams.push(`page=${encodeURIComponent(page)}`);
    }
    if (variable) {
      queryParams.push(`variable=${encodeURIComponent(variable)}`);
    }

    const queryString = queryParams.length > 0 ? `?${queryParams.join('&')}` : '';

    return this.GetAll(API_URL + API_ENDPOINTS.Branch + `/GetAgentRelatedBranchWithOpenBalanceByMonthOrDate?branchId=${encodeURIComponent(branchId)}&${queryString}`);
  }
  GetAgentTransactionalHistoryByBranches(agentId,from, to, branches: string[]): Observable<BaseResponse> {
    const params = new HttpParams()
      .set('from', from)
      .set('to', to)
      .set('branches', branches.join(','))
      .set('agentId',agentId);

    return this.http.get(API_URL+API_ENDPOINTS.Branch+"/GetAgentTransactionalHistoryByBranches", { params }) as Observable<BaseResponse>;
  }

}
