import { Component, OnInit } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { Agent } from "src/app/Model/Agent";
import { AgentService } from 'src/app/services/agent/agent.service';
import { AlertService } from 'src/app/services/alert.service'; 
class FilterObject {
  constructor(private data: Agent) { }


  name: string = this.data.name;

}
@Component({ 
  selector: 'app-view-agent',
  templateUrl: './view-agent.component.html',
  styleUrls: ['./view-agent.component.css']
})
export class ViewAgentComponent implements OnInit {
  search: string;
  listData: Agent[] = [];
  listDataCopy: string;
  isvisible = false;
  modalTitle = "Create new Agent";
  AgentObserSubject: Subject<Agent> = new Subject();
  contactSubject: Subscription;
  checked = false;
  showAgent = false;
  agent = new Agent();
  

  constructor(private _AgentService: AgentService,
    private _notification: AlertService) { }

  ngOnInit(): void {
    this._AgentService.agentObserve$.subscribe(res => {
      if (res) {
        this.listData = res;
        this.listDataCopy = JSON.stringify(this.listData);
      }
      this.isvisible = false;
    });
    this._AgentService.GetAgent();
  }
  filter() {
    this.listData = JSON.parse(this.listDataCopy);
    if (this.search !== "") {
      this.listData = this.listData.filter(item => {
        let data = new FilterObject(item);
        return Object.keys(data).some(
          k =>
            data[k] != null &&
            data[k]
              .toString()
              .toLowerCase()
              .includes(this.search.toLowerCase())
        )
      });
    }
  }
  handleCancel() {
    this.isvisible = false;
    this.showAgent = false;
  }
  handleOpen() {
    this.isvisible = true;
    this.modalTitle = "New Agent";
    this.AgentObserSubject.next(null);
    this.showAgent = false;
  }
  handleEdit(data: Agent) {
    this.modalTitle = "Edit User";
    this.isvisible = true;
    this.showAgent = false;
    this.AgentObserSubject.next(data);
  }
  showSingleAgent(data: Agent) {
    this.agent = data;
    this.showAgent = true;
  }
  DeleteAgent(data: Agent) { 
    this._notification.delete().then(result => {
      if (result.isConfirmed) {
        this._AgentService.DeleteAgent(data.id).subscribe(res => {
          if (res.isSuccessfull) {
            this._AgentService.GetAgent();
            this._notification.success('agent Deleted sucessfully!')
          }
          else {
            this._notification.error('Error while Deleteing agent!')
          }
        });
      }
      else {
        return;
      }

      this.showAgent = false;
    });
  }

}
