import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { AlertService } from 'src/app/services/alert.service';
import { Input } from '@angular/core';
import {Agent} from "src/app/Model/Agent";
import { AgentService } from "src/app/services/agent/agent.service";
import { BranchService } from 'src/app/services/branch/branch.service';
import { BranchDTO } from '../../branch/view-branch/view-branch.component';

@Component({
  selector: 'app-detailview-agent',
  templateUrl: './detailview-agent.component.html',
  styleUrls: ['./detailview-agent.component.css']
})
export class DetailviewAgentComponent implements OnInit {
  @Input() agentSubject:Subject<Agent>;

  form:FormGroup;
  AgentId:number;
  isEditMode=false;
  loading=false;

  constructor(private fb:FormBuilder,
    private _notification:AlertService,
    private _branchService:BranchService,
    private _agentService:AgentService) {
      this.form=fb.group({
        name:[null, [Validators.required]],
        openingBalance:[null],
        branchId:[null,[Validators.required]],
      });
     }
     get f() {return this.form.controls}
branches:BranchDTO[];
  ngOnInit(): void {

    this._branchService.branchObserver$.subscribe(res=>{

      if(res){
        this.branches=res.branches;
      }
    });
    this._branchService.GetBranches();

    this.agentSubject.subscribe(res =>{
      if(res){
    this.form.patchValue(res);
   this.AgentId=res.id;
    this.isEditMode=true;
      }
      else{
        this.form.reset();
        this.AgentId=null;
        this.isEditMode=false;
      }
    });
  }
  handleSubmit(){
    if(this.form.invalid){
      return
   }
    this.loading=true;
    let data = this.form.value as Agent;
    if(this.isEditMode){
      data.id=this.AgentId;
      this._agentService.UpdateAgent(data).subscribe(res=>{
        this.loading=false;
        if(res.isSuccessfull){

          this._agentService.GetAgent();
          this._notification.success('Agent Update successfully!');
        }
        else{
         this._notification.error(' Error While Updating Agent!');
        }
      });
    }
  else{
    this.loading=false;

    this._agentService.SaveAgent(data).subscribe(res=>{
      if(res.isSuccessfull){
        console.log('check')

        this._agentService.GetAgent();
        this._notification.success('Agent Save successfully!');
      }
     else{
        this._notification.error(' Error While Saving Agent!');
      }

      });
    }
  }


  resetForm(){
    this.form.setValue({
     name:null,
     openingBalance:null
   });
 }
}





