import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { BehaviorSubject, Observable } from 'rxjs';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';
import { Agent } from "src/app/Model/Agent";
import { BaseEntity } from 'src/app/Model/BaseEntity';


@Injectable({
  providedIn: 'root'
})
export class AgentService extends BaseApiHandlerService {
  agentObserve$:Observable<Agent[]>;
  private agentSubject$: BehaviorSubject<Agent[]> = new BehaviorSubject<Agent[]>(undefined);

  public get Agent() {return this.agentSubject$.value} 


  constructor(private http:HttpClient) {
    super(http);
    this.agentObserve$= this.agentSubject$.asObservable();

   }
   SaveAgent(data){
    return this.Post(data,API_URL+API_ENDPOINTS.Agent);
  }
  UpdateAgent(data:Agent){
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.Agent);
  }
  GetAgent(){
    return this.GetAll(API_URL+API_ENDPOINTS.Agent).subscribe(res =>{
      this.agentSubject$.next(res.dynamicResult);
    });
  }
  DeleteAgent(id){
     return this.Delete(id,API_URL+API_ENDPOINTS.Agent);
   }
}
